
.App {
    font-family: sans-serif;
    text-align: center;
  }
  
  .dialog {
    box-shadow: 0 8px 6px -6px black;
    position: static;
    left: 20%;
    top: 10%;
  }
  
  .image {
    width: 300px;
  }
  
  .divElement {
    position: fixed;
    top: 50%;
    left: 50%;
    padding: 6%;
    z-index: 3;
    transform: translate(-50%, -50%);
}
  
.background {
    position: fixed;
    top: 50%;
    left: 50%;
    z-index: 3;
    transform: translate(-50%, -50%);
}
